import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition: "fixed",
			headerTop: "0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0,
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			goodsList: [],
			cateId: 0,
			keywords: '',
			pageNo: 1,
			isVip: false
		};
	},
	onShow() {
		this.isVip = this.$api.isVip()
	},
	onLoad(options) {



		this.cateId = options.tid ? options.tid : 0;
		this.keywords = options.keywords ? options.keywords : ''
		this.loadData();
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom() {
		this.loadData();
	},
	methods: {
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type = 'add', loading) {
			//没有更多直接返回
			if (type === 'add') {
				if (this.loadingType === 'nomore') {
					return;
				}
				this.loadingType = 'loading';
			} else {
				this.loadingType = 'more'
			}

			let orderByInfo = {}
			if (this.filterIndex === 0) {
				//销量排序
				orderByInfo = {
					orderBy: 'sales',
					isAsc: false
				}
			}
			if (this.filterIndex === 1) {
				//价格排序 需要从新获取Page
				orderByInfo = {
					orderBy: 'price',
					isAsc: this.priceOrder === 1
				}
			}
			if (type === 'refresh') {
				this.pageNo = 1
			}
			this.$api.request('goods', 'getGoodsPage', {
				categoryId: this.cateId,
				title: this.keywords,
				pageNo : this.pageNo,
				...orderByInfo
			}).then(res => {
				let tempList = res.data.items
				if (type === 'refresh') {
					this.goodsList = [];
				}
				this.goodsList = this.goodsList.concat(tempList);
				this.pageNo = res.data.pageNo + 1
				this.loadingType = res.data.totalPageNo > res.data.pageNo ? 'more' : 'nomore';
				if (type === 'refresh') {
					if (loading == 1) {
						uni.hideLoading()
					} else {
						uni.stopPullDownRefresh();
					}
				}
			})
		},
		//筛选点击
		tabClick(index) {
			if (this.filterIndex === index && index !== 1) {
				return;
			}
			this.filterIndex = index;
			if (index === 1) {
				this.priceOrder = this.priceOrder === 1 ? 2 : 1;
			} else {
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.id;
			uni.navigateTo({
				url: `/pages/product/detail?id=${id}`
			})
		},
		stopPrevent() {}
	},
}
